ul li {list-style-type: georgian}

.app-wrapper{
    display: grid;

    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 3fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content{
    grid-area: c;
    background-color: #d5efff;
}


